<template>
    <div class="vue-doc-wrapper">
        
        <div class="vue-doc-title">
          <h2 id="vue-doc-快速上手1" class="vue-doc-element">快速上手</h2>
<p class="vue-doc-element">使用 webpack 作为构建工具</p>

        </div>
        <div class="vue-doc-title-EN">
          <h2 id="vue-doc-start1" class="vue-doc-element">start</h2>
<p class="vue-doc-element">null</p>

        </div>

        


        <div class="vue-doc-content">
          <h2 id="vue-doc-引入ivuel1" class="vue-doc-element">引入 ivuel</h2>
<p class="vue-doc-element">一般在 webpack 入口页面 main.js 中如下配置:</p>
<pre class="vue-doc-element"><code class="lang-js"><span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>;
<span class="hljs-keyword">import</span> ivuel <span class="hljs-keyword">from</span> <span class="hljs-string">'ivuel'</span>;
<span class="hljs-keyword">import</span> <span class="hljs-string">'ivuel/dist/styles/ivuel.css'</span>;
Vue.use(ivuel);
</code></pre>
<h2 id="vue-doc-按需引用2" class="vue-doc-element">按需引用</h2>
<pre class="vue-doc-element"><code class="lang-base">npm <span class="hljs-keyword">install</span> babel-<span class="hljs-keyword">plugin</span>-<span class="hljs-keyword">import</span>-ivuel <span class="hljs-comment">--save-dev</span>

</code></pre>
<p class="vue-doc-element"><code class="vue-doc-element">.babelrc</code>配置</p>
<pre class="vue-doc-element"><code class="lang-json">{
  <span class="hljs-attr">"plugins"</span>: [[<span class="hljs-string">"import-ivuel"</span>, {
    <span class="hljs-attr">"libraryName"</span>: <span class="hljs-string">"ivuel"</span>,
    <span class="hljs-attr">"libraryDirectory"</span>: <span class="hljs-string">"src/components"</span>
  }]]
}
</code></pre>
<p class="vue-doc-element">或者(按需引进包括样式)</p>
<pre class="vue-doc-element"><code class="lang-json">{
  <span class="hljs-attr">"plugins"</span>: [[<span class="hljs-string">"import-ivuel"</span>, {
    <span class="hljs-attr">"libraryName"</span>: <span class="hljs-string">"ivuel"</span>,
    <span class="hljs-attr">"libraryDirectory"</span>: <span class="hljs-string">"src/components"</span>,
    <span class="hljs-attr">"style"</span>:<span class="hljs-string">"ivuel"</span>
  }]]
}
</code></pre>
<p class="vue-doc-element">然后这样按需引入组件，就可以减小体积了：</p>
<pre class="vue-doc-element"><code class="lang-js">import { Button, <span class="hljs-keyword">Table</span> } from <span class="hljs-comment">'ivuel'</span>;
Vue.component(<span class="hljs-string">'Button'</span>, Button);
Vue.component(<span class="hljs-string">'Table'</span>, <span class="hljs-keyword">Table</span>);
</code></pre>
<p class="vue-doc-element">特殊组件引进</p>
<pre class="vue-doc-element"><code class="lang-js">import { Poptip,<span class="hljs-built_in">Tooltip</span>,Message,Modal,Spin,Notice,LoadingBar } from <span class="hljs-string">'ivuel'</span><span class="hljs-comment">;</span>
<span class="hljs-comment">;</span>
Vue.directive(<span class="hljs-string">'poptip'</span>, Poptip.directive)<span class="hljs-comment">;</span>
Vue.directive(<span class="hljs-string">'tooltip'</span>, <span class="hljs-built_in">Tooltip</span>.directive)<span class="hljs-comment">;</span>
Vue.component(<span class="hljs-string">'Tooltip'</span>, <span class="hljs-built_in">Tooltip</span>)<span class="hljs-comment">;</span>
Vue.component(<span class="hljs-string">'Poptip'</span>, Poptip)

Vue.component(<span class="hljs-string">'Modal'</span>, Modal)<span class="hljs-comment">;</span>
Vue.component(<span class="hljs-string">'Spin'</span>, Spin)<span class="hljs-comment">;</span>
Vue.component(<span class="hljs-string">'Notice'</span>, Notice)<span class="hljs-comment">;</span>
Vue.component(<span class="hljs-string">'LoadingBar'</span>, LoadingBar)<span class="hljs-comment">;</span>
Vue.component(<span class="hljs-string">'Message'</span>, Message)<span class="hljs-comment">;</span>

Vue.prototype.$Loading = LoadingBar<span class="hljs-comment">;</span>
Vue.prototype.$Message = Message<span class="hljs-comment">;</span>
Vue.prototype.$Modal = Modal<span class="hljs-comment">;</span>
Vue.prototype.$Notice = Notice<span class="hljs-comment">;</span>
Vue.prototype.$Spin = Spin<span class="hljs-comment">;</span>
</code></pre>
<h3 id="vue-doc-特别提醒2_1" class="vue-doc-element">特别提醒</h3>
<ul class="vue-doc-element">
<li>前一种按需引用仍然需要导入样式，即在 main.js 或根组件执行 import &#39;ivuel/dist/styles/ivuel.css&#39;;</li>
<li><p class="vue-doc-element">按需引进样式的话,通过<code class="vue-doc-element">less-loader</code>的<code class="vue-doc-element">modifyVars</code>配置皮肤</p>
<pre class="vue-doc-element"><code class="lang-json"><span class="hljs-comment">//package.json</span>
{
  <span class="hljs-string">"name"</span>: <span class="hljs-string">"vue-project"</span>,
  <span class="hljs-string">"version"</span>: <span class="hljs-string">"1.0.0"</span>,
  ...
  <span class="hljs-string">"theme"</span>:{<span class="hljs-comment">//此处修改样式变量</span>
    <span class="hljs-string">"@primary-color"</span>:<span class="hljs-string">"red"</span>
  }
}

</code></pre>
<pre class="vue-doc-element"><code class="lang-js">  <span class="hljs-comment">//vue-cli /build/utils.js</span>
  const packageConfig = require(<span class="hljs-string">'../package.json'</span>)
  <span class="hljs-keyword">return</span> {
    <span class="hljs-string">css :</span> generateLoaders(),
    <span class="hljs-string">postcss :</span> generateLoaders(),
    <span class="hljs-string">less :</span> generateLoaders(<span class="hljs-string">'less'</span>,{<span class="hljs-string">"modifyVars"</span>:packageConfig.theme}),<span class="hljs-comment">//修改此行</span>
    <span class="hljs-string">sass :</span> generateLoaders(<span class="hljs-string">'sass'</span>, { <span class="hljs-string">indentedSyntax:</span> <span class="hljs-literal">true</span> }),
    <span class="hljs-string">scss :</span> generateLoaders(<span class="hljs-string">'sass'</span>),
    <span class="hljs-string">stylus :</span> generateLoaders(<span class="hljs-string">'stylus'</span>),
    <span class="hljs-string">styl :</span> generateLoaders(<span class="hljs-string">'stylus'</span>)
  }
</code></pre>
</li>
</ul>
<h2 id="vue-doc-配置ivuelloader3" class="vue-doc-element">配置<code class="vue-doc-element">ivuel-loader</code></h2>
<h3 id="vue-doc-下载3_1" class="vue-doc-element">下载</h3>
<pre class="vue-doc-element"><code class="lang-bash">npm <span class="hljs-selector-tag">i</span> ivuel-loader -D
</code></pre>
<h3 id="vue-doc-配置3_2" class="vue-doc-element">配置</h3>
<pre class="vue-doc-element"><code><span class="hljs-attr">module:</span> <span class="hljs-string">{</span>
<span class="hljs-attr">    rules:</span> <span class="hljs-string">[</span>
        <span class="hljs-string">{</span>
<span class="hljs-attr">            test:</span> <span class="hljs-string">/\.vue$/,</span>
<span class="hljs-attr">            use:</span> <span class="hljs-string">[</span>
                <span class="hljs-string">{</span>
<span class="hljs-attr">                    loader:</span> <span class="hljs-string">'vue-loader'</span><span class="hljs-string">,</span>
<span class="hljs-attr">                    options:</span> <span class="hljs-string">{}</span>
                <span class="hljs-string">},</span>
                <span class="hljs-string">{</span>
<span class="hljs-attr">                    loader:</span> <span class="hljs-string">'ivuel-loader'</span><span class="hljs-string">,</span>
<span class="hljs-attr">                    options:</span> <span class="hljs-string">{</span>
<span class="hljs-attr">                        prefix:</span> <span class="hljs-literal">false</span>
                    <span class="hljs-string">}</span>
                <span class="hljs-string">}</span>
            <span class="hljs-string">]</span>
        <span class="hljs-string">}</span>
    <span class="hljs-string">]</span>
<span class="hljs-string">}</span>
</code></pre>
        </div>
        <div class="vue-doc-content-EN">
          
        </div>
        


    </div>
  </template>
  <script>
      
  
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


